import React from 'react';
import PropTypes from 'prop-types';
import {
  Panel,
  PanelHeader,
  Spinner,
  Button,
  FormLayout,
  PromoBanner
} from '@vkontakte/vkui';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';

import styles from './Home.module.css';
import SearchForm from '../organisms/SearchForm';
import './HomePanelIn.css';

const Home = ({
  id,
  isInitialized,
  selectCity,
  selectStation,
  selectSpeciality,
  onSearch,
  withSubway,
  onJoinGroup,
  bannerData
}) => (
  <Panel id={id} theme="white">
    <PanelHeader theme="alternate" noShadow />
    <div className={styles.Home}>
      <div className={styles.Content}>
        <div className={styles.Header}>Doc Online</div>
        <div className={styles.Subheader}>
          Бесплатный сервис по поиску врачей
        </div>
        {isInitialized ? (
          <SearchForm
            selectCity={selectCity}
            selectSpeciality={selectSpeciality}
            selectStation={selectStation}
            withSubway={withSubway}
            onSubmit={onSearch}
          />
        ) : (
          <Spinner size="large" />
        )}
        <FormLayout>
          <Button
            size="xl"
            before={<Icon24LogoVk />}
            onClick={() => onJoinGroup()}
          >
            Подписаться
          </Button>
        </FormLayout>
      </div>
    </div>
    {bannerData != null && <PromoBanner bannerData={bannerData} />}
  </Panel>
);

Home.defaultProps = {
  bannerData: null
};

Home.propTypes = {
  id: PropTypes.string.isRequired,
  isInitialized: PropTypes.bool.isRequired,
  selectCity: PropTypes.func.isRequired,
  selectStation: PropTypes.func.isRequired,
  selectSpeciality: PropTypes.func.isRequired,
  onSearch: PropTypes.func.isRequired,
  withSubway: PropTypes.bool.isRequired,
  onJoinGroup: PropTypes.func.isRequired,
  bannerData: PropTypes.shape()
};

export default Home;
